input:not([type=file]), textarea {
    -webkit-appearance: none;
    background-color: #fafbfc;
    border: none;
    border-radius: 3px;
    box-shadow: inset 0 0 0 2px #dfe1e6;
    box-sizing: border-box;
    display: block;
    line-height: 20px;
    margin-bottom: 12px;
    outline: none;
    padding: 8px 12px;
    transition-duration: 85ms;
    transition-property: background-color, border-color, box-shadow;
    transition-timing-function: ease
}

input:not([type=file]).full, textarea.full {
    width: 100%
}

input:not([type=file]).input-error, textarea.input-error {
    box-shadow: inset 0 0 0 2px #eb5a46
}

input:not([type=file]):focus, textarea:focus {
    outline: 0
}

input::placeholder, textarea::placeholder {
    color: #5e6c84
}

input[type=file] {
    margin-bottom: 16px
}

input[type=email]:invalid {
    box-shadow: none
}

input[type=hidden] {
    display: none
}

.check-div, .radio-div {
    display: block;
    margin: 0 0 4px 20px;
    min-height: 20px;
    position: relative
}

.check-div input, .radio-div input {
    box-shadow: none;
    left: -18px;
    margin: 0;
    min-height: 0;
    padding: 0;
    position: absolute;
    top: 3px
}

.check-div label, .radio-div label {
    color: #172b4d;
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
    margin: 0
}

input[type=radio] {
    -webkit-appearance: radio;
    display: inline;
    min-height: inherit
}

input[type=checkbox] {
    -webkit-appearance: checkbox;
    display: inline;
    margin-right: 4px
}

.check-div {
    margin: 8px 6px;
    min-height: 16px;
    padding-left: 34px;
    position: relative
}

.check-div label {
    line-height: 16px
}

.check-div-inline {
    display: inline-flex
}

.check-div input[type=checkbox] {
    height: 16px;
    left: 0;
    opacity: 0;
    position: absolute;
    top: 0;
    width: 16px
}

.check-div input[type=checkbox] + label.quiet {
    color: #5e6c84
}

.check-div input[type=checkbox] + label:before {
    background-color: #fafbfc;
    border-radius: 2px;
    box-shadow: inset 0 0 0 2px #dfe1e6;
    content: "";
    flex-shrink: 0;
    height: 16px;
    left: 0;
    margin: 0;
    overflow: hidden;
    position: relative;
    position: absolute;
    top: 0;
    transition: all .2s ease-in-out;
    white-space: nowrap;
    width: 16px
}

